import React, { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";

import { Row, Col, Form, Input, Button, Checkbox, Alert } from "antd";
import { v4 as uuidv4 } from "uuid";
import { browserName } from "react-device-detect";
import { useHistory } from "react-router";
import { connect } from "react-redux";
import { RiFacebookFill } from "react-icons/ri";
import LeftContent from "../leftContent";
import logo from "../../../../assets/images/logo/wheelsmart_logo_color.svg";
import NumericInput from "../../../components/data-entry/inputs/numberInput";
import Timer from "./timer";

// actions
import {
  checkLogin,
  InputChangeValue,
  cleardata,
  getOTPRequest,
  getResendOTP,
  showResend
} from "../../../../store/auth/login/loginSlice";
import { func } from "prop-types";

function MobileNumberLogin(props) {
  const [showResentOTP, setShowResentOTP] = useState(false);
  const { time } = props;
  const history = useHistory();
  const uuid = uuidv4();
  const [form] = Form.useForm();
  useEffect(() => {
    props.cleardata();
    if (localStorage.getItem("access_token")) {
      history.push("/");
    }
  }, []);

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
  const onFinish = () => {
    if (props.OTPSend) {
      var loginData = {
        password: props.loginData.password,
        username: props.loginData.username,
        device_uuid: uuid,
        device_name: browserName,
        device_type: 3, // web
      };
      props.checkLogin({
        loginData,
        history,
      });
    } else {
      props.getOTPRequest({ username: props.loginData.username });
    }
  };



  function resendOTP(){
    props.getResendOTP({ username: props.loginData.username });
  }
  


  useEffect(() => {
    form.setFieldsValue({
      password: props.loginData.password,
      username: props.loginData.username,
    });
  }, [props.loginData]);
  return (
    <Row gutter={[32, 0]} className="hp-authentication-page">
      <LeftContent />

      <Col lg={12} span={24} className="hp-py-sm-0 hp-py-md-64">
        <Row className="hp-h-100" align="middle" justify="center">
          <Col>
            <div className="hp-m-0 hp-p-0">
              <img
                src={logo}
                style={{
                  height: 100,
                  width: 500,
                }}
                alt="Logo"
              />
            </div>
            <div
              xxl={11}
              xl={15}
              lg={20}
              md={20}
              sm={24}
              className="hp-px-sm-8 hp-pt-24 hp-pb-48"
            >
              <h1 className="hp-mb-sm-0">Login</h1>
              <p className="hp-mt-sm-0 hp-mt-8 hp-text-color-black-60">
                Welcome back, please login to your account.
              </p>

              <Form
                layout="vertical"
                name="loginWithPhone"
                className="hp-mt-sm-16 hp-mt-32"
                form={form}
                onFinish={onFinish}
              >
                <Form.Item
                  label="Enter your mobile number:"
                  name="username"
                  rules={[
                    {
                      required: true,
                      message: "Please input your mobile number!",
                    },
                    {
                      min: 10,
                      message: "Phone Number Must be 10 number",
                    },
                  ]}
                  getValueProps={() => {}}
                  hasFeedback
                >
                  <NumericInput
                    id="username"
                    name="username"
                    style={{ width: "100%" }}
                    placeholder="Mobile Number"
                    maxLength={10}
                    value={
                      props.loginData.username == null
                        ? ""
                        : props.loginData?.username
                    }
                    onChange={props.InputChangeValue}
                  />
                </Form.Item>
                {props.OTPSend && (
                  <>
                    <Form.Item
                      label="Enter your OTP:"
                      name="password"
                      rules={[
                        {
                          required: true,
                          message: "Please input your OTP!",
                        },
                      ]}
                      getValueProps={() => {}}
                      hasFeedback
                    >
                      <NumericInput
                        id="password"
                        name="password"
                        style={{ width: "100%" }}
                        placeholder="OTP"
                        maxLength={4}
                        value={
                          props.loginData.password == null
                            ? ""
                            : props.loginData?.password
                        }
                        onChange={props.InputChangeValue}
                      />
                    </Form.Item>
                  </>
                )}
                {/* <Form.Item
                label="Enter your mobile number :"
                className="hp-mb-16"
                rules={[
                  {
                    required: true,
                    message: "Please input your phone Number!",
                  },
                ]}
              >
                <Input id="error" />
              </Form.Item> */}

                {/* <Form.Item label="OTP :" className="hp-mb-8">
                <Input.Password id="warning2" />
              </Form.Item> */}
                {props.loginError?.detail && (
                  <Alert message={props.loginError?.detail} type="error" />
                )}

                <Form.Item className="hp-mt-16 hp-mb-8">
                  {props.OTPSend ? (

                  <Button block type="primary" htmlType="submit">
                    Submit
                  </Button>
                    
                  ) : (
                    <Button block type="primary" htmlType="submit">
                    REQUEST OTP
                    </Button>
                  )}
                  </Form.Item>
              </Form>

              <Row>
                <Col span={24} md={12}>
                  <div className="hp-form-info">
                    <Link
                      className="hp-text-color-primary-1 hp-text-color-dark-primary-2 hp-caption"
                      to="/login"
                    >
                      Login with UserName
                    </Link>
                  </div>
                </Col>

                {props.OTPSend && (
                  <Col span={24} md={12}>
                    {props.showResentOTP ? (
                      <div
                        className="hp-form-info hp-float-right"
                        onClick={resendOTP}
                      >
                        Resend OTP
                      </div>
                    ) : (
                      <div className="hp-form-info hp-float-right">
                        Resent OTP In
                        <Timer
                          time={time}
                          showTimer={!props.showResentOTP}
                          timeStop={() => {
                            props.showResend(true);
                          }}
                        />
                      </div>
                    )}
                  </Col>
                )}
              </Row>
            </div>
          </Col>
        </Row>
      </Col>
    </Row>
  );
}
function mapStateToProps({ login }) {
  return {
    loginError: login.loginError,
    loginData: login.loginData,
    OTPSend: login.OTPSend,
    showResentOTP: login.showResentOTP
  };
}

export default connect(mapStateToProps, {
  InputChangeValue,
  cleardata,
  checkLogin,
  getOTPRequest,
  getResendOTP,
  showResend
})(MobileNumberLogin);
